<template>
  <div id="common">
    <van-row style="padding:40px;">
      <van-col span="24">
        <div style="text-align:center;">
          <img src="../../assets/images/login/login.png" alt />
        </div>
      </van-col>
      <van-col span="24">
        <div style="text-align:center;font-size:22px;">登录页面</div>
      </van-col>
    </van-row>
    <div class="login">
      <van-cell-group>
        <van-field
          v-model="username"
          required
          clearable
          label="用户名"
          right-icon="question-o"
          placeholder="请输入用户名"
        />

        <van-field v-model="password" type="password" label="密码" placeholder="请输入密码" required />
      </van-cell-group>

      <van-button type="primary" size="large" @click="submit">登录</van-button>
    </div>
  </div>
</template>
<script>
import { sendSock } from "../../socket/socket";
import { Dialog, Toast } from "vant";
export default {
  data() {
    return {
      username: "",
      password: "",
      loginFlag: false
    };
  },
  methods: {
    submit: function() {
      if (this.username == null || this.username.length == 0) {
        Dialog.alert({ title: "登录失败", message: "用户不能为空" });
        return;
      } else if (this.password == null || this.password.length == 0) {
        Dialog.alert({ title: "登录失败", message: "密码不能为空" });
        return;
      }
      this.$http
        .post(
          "/api/user/login",
          "username=" + this.username + "&password=" + this.password
        )
        .then(response => {
          let result = response.data;
          // console.log(result);
          if (!result || result.code != 200) {
            Dialog.alert({ title: "登录失败", message: result.message });
            return;
          } else {
            //底部工作消息数量
            this.$http.get("/api/app/appList").then(res => {
              this.$store.state.newWorkCount = res.data.data.total;
            });

            this.$http.get("/api/user/isLogin").then(res => {
              // console.log(res);
              if (res.status === 200) {
                localStorage.setItem("loginFlag", res.data);
                this.loginFlag = res.data;
              }
            });
            let ctx = this,
              user = result.data;
            //存储用户个人信息
            this.$store.commit("getUserInfo", result.data);
            localStorage.setItem("userInfo", JSON.stringify(result.data));
            console.log(result);
            //连接websocket
            // this.$parent.sendSock(result.data.userId);
            sendSock(result.data.userId);

            Toast({
              type: "success",
              message: result.message,
              icon: "success",
              duration: 500,
              onClose: function() {
                ctx.$router.push({ path: user.homeUrl });
              }
            });
          }
        });
    }
  }
};
</script>
<style>
#common {
  height: 100%;
  width: 100%;
  background: url(../../assets/images/IMG_3299.jpg) no-repeat;
  background-size: cover;
  position: fixed;
}
.login {
  /* padding: 40px 0; */
}

.van-button {
  margin-top: 20px;
}
</style>



